.Hero {
  flex-grow: 1;
  background: url(/hot-foods-recipe/static/media/hero-image.b57fcde7.jpg);
  width: 100%;
  height: 70vh;
  display: flex;
  align-items: center;
  justify-content: center;
  background-size: cover;
  background-position: center;
  background-color: #000000bd;
  background-blend-mode: hue;
  color: #fff;
}

.Hero h1 {
  font-size: 5rem;
  font-weight: 700;
}

.Hero h2 {
  font-size: 4rem;
  font-weight: 500;
}

.Home {
  display: flex;
  flex-flow: column;
  height: 100vh;
}

